@import url(https://fonts.googleapis.com/css2?family=Quantico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.list{
  padding: 0;
  background: #000;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0 -.25rem;
}
#overlay{
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
    background: rgba(0,0,0,0.8);
    z-index: 12;
}

.overlay__wrapper{
	position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
	width: 100%;
	max-width: 35rem;
}

.overlay__wrapper__video-container{
	width: 100%;
	height: 0;
	padding-bottom: 56.25%;
	position: relative;
}
.overlay__wrapper__video-container::after{
    content: "X";
    display: block;
    font-weight: bold;
    font-size: 1rem;
    color: #FFF;
    padding: .5rem;
    position: absolute;
    top: -2.5rem;
    right: 1rem;
    border: 1px solid #CCC;
    pointer-events: none;
}

.overlay__wrapper__video-container__video{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

@media (min-width: 37.5625rem) {
	.overlay__wrapper__video-container::after{
		right: 0;
	}
}

@media screen and (min-width: 48rem) {   
    .overlay__wrapper{
        width: 80vw;
    }
}

@media (min-width: 70rem){
	.overlay__wrapper{
		max-width: 56.25rem;
	}
}
#main-header{
    background: #000;
    position: relative;
    text-align: center;
    margin-top: 1.5rem;
    margin-bottom: 1rem;
}

.header-main{
    font-size: 2rem;
    font-family: 'Quantico', sans-serif;
    text-align: center;
    color: #FFF;
    margin: 0;
    padding: 0 0.5rem;
    display: inline;
    border-bottom: 1px solid #FFF;
}
.header-main span{
    color: #F00
}
.header-main::before,.header-main::after{
    content: "\27A4";
    color:#F00;
    display: inline-block;
}
.header-main::after{
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}

@media screen and (min-width: 48rem) {   
    header{
        max-width: 45rem;
        margin: 0 auto;
    }

    #main-header{
        margin: 0;
        text-align: left;
    }
}

@media screen and (min-width: 64rem) {
    header{
        width: 100%;
        max-width: 64rem;
        margin: 0 auto;
    }
}
.search-box{
    margin-left: auto;
}

.search-box__container{
    width: 100%;
    padding: 1rem;
    position: relative;
}

#search{
    padding: 0.375rem;
    width: 80%;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    display: inline;
    border: 0;
}

.btn-wrapper{
    background: #ccc;
    position: relative;
    padding: 0.575rem 1rem;
    display: inline;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.btn-search{
    padding: 0.25rem;
    background: center / 60% no-repeat url(/what-to-watch-react/static/media/magnifying-glass-icon.82481d53.svg);
    text-indent: -9999px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    border: 0;
}

.autocomplete{
    position: absolute;
    top: 3.5rem;
    left: 1rem;
    background: #ffffff;
    z-index: 9;
    padding: 0.25rem 0.5rem;
    border: 1px solid rgba(0,0,0,.4);
}
.autocomplete li{
    list-style-type: none;
}
.hidden{
    display: none;
}

.film-suggestion{
    cursor: pointer;
}

.film-suggestion:hover{
    font-weight: bold;
}

@media screen and (min-width: 48rem) {   
    #search{
        width: auto;
        width: initial;
        max-width: 15.9375rem;
    }

    .autocomplete{
        width: 100%;
        max-width: 17.9375rem;
    }

    .btn-wrapper{
        width: 15%;
    }
}
.runtime-filter{
    box-sizing: border-box;
    display: inline-block;
    position: relative;
    border-radius: 4px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    font-weight: bold;
    margin: 0.5rem 0;
    width: calc(50% - 0.5rem);
    text-align: left;
}

.runtime-filter:nth-child(odd){
    margin-left: 0.5rem;
    margin-right: 0;
}

.runtime-filter:last-child{
    margin-left: 0;
    margin-right: 0;
    padding-left: 0.5rem;
    width: 100%;
}

@media screen and (min-width: 30rem) {
    .runtime-filter{
        text-align: center;
    }
}

@media screen and (min-width: 48rem) {   
    .runtime-filter{
        width: calc(25% - 0.5rem);
    }
    .runtime-filter:last-child{
        width: 100%;
    }
}

@media screen and (min-width: 64rem) {
    .runtime-filter, .runtime-filter:last-child{
        width: calc(20% - 0.5rem);
    }
}
#runtimes{
    padding: 0;
    margin: 0;
}

#runtimes-filters{
    margin: 0 auto;
}

@media screen and (min-width: 64rem) {
    #runtimes-filters{
        width: 100%;
        margin: 0 auto;
    }
}
#filter-decades{
    padding: 1rem;
    position: relative;
    text-align: center;
}

.film-filter{
    background: #000;
    color: #FFF;
    padding: 1rem;
    border: 1px solid #6c757d;
}

.filter-header{
    font-size: 1.25rem;
    font-weight: normal;
    margin: 0 0 1rem;
    padding: 0;
    text-align: center;
}

.decades-filters__label{
    padding: 0 0.5rem;
}

#oldest-decade, #newest-decade{
    font-family: 'Quantico', sans-serif;
    background: #FFF;
    color: #000;
    font-size: 1.25rem;
}
#extra-genres .genre-selector{
    padding: 1.6rem 0;
    
}

.genre-selector,.decade-selector{
    padding: 1rem;
    display: inline-block;
}

.genre-selector {
    text-align: center;
    width: 33.33%;
}

.genre-selector label{
    text-transform: capitalize;
}

.genre-items{
    width: 100%;
    display: block;
    text-align: center;
}

@media screen and (min-width: 30rem) {
    .genre-selector{
        width: 33.33%;
    }
}

@media screen and (min-width: 48rem) {   
    .genre-selector{
        width: 15%;
    }
    #extra-genres .genre-selector{
        width: 20%;
    }
}
#filter-genres{
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
}

#main-genres,#extra-genres{
    text-align: center;
}
.cta-expand{
    background: rgba(255, 255, 255, 0.2);
    display: block;
    text-align: center;
    text-decoration: none;
    color:#FFF;
    font-size: 1rem;
    font-weight: bold;
    padding: 0.5rem 0;
    outline: none;
}
.cta-expand:hover{
    color: #FF0
}

.cta-expand::after{
    padding-left: 1rem;
    display: inline-block;
    content:"[ + ]";
    color: #FF0;
    transition: all ease-out .75s;
}
.cta-expand:hover::after{
    color:#FFF;
}

.cta-expand.expanded::after{
    content:"[ - ]";
}

#extra-genres{
    overflow: hidden;
    max-height: 0;
    transition: max-height .75 ease-out;
}

.cta-expand.expanded + #extra-genres{
    max-height: 6.25rem;
    transition: max-height .75s ease-in;
}

@media screen and (min-width: 48rem) {   
    #extra-genres{
        width: 80%;
        margin: 0 auto;
    }
}
#filter-watched .btn-outline-secondary{
    background: #CCC;
    color: #000;
}

.btn-outline-secondary:not(:disabled):not(.disabled).btn-filter.active{
    background: #000;
    box-shadow: none;
    border-bottom: 0;
    position: relative;
}

.btn-outline-secondary:not(:disabled):not(.disabled).btn-filter.active:hover{
    box-shadow: none;
}

.btn-outline-secondary:not(:disabled):not(.disabled).btn-filter.active::after{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: -2px;
    right: 0;
    background: #000;
    z-index: 1;
}

.btn-outline-secondary.btn-vod{
    background: #A80000;
    color: #FFF;
}
.btn-outline-secondary.btn-trailer{
    background: #000;
    color: #FFF;
}
.item{
    list-style-type: none;
    overflow: hidden;
    position: relative;
    margin: .25rem;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(100% - .5rem);
    flex-grow: 0;
}

.item:hover{
    overflow: visible;
}

.item .item-container:hover{
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    overflow: visible;
    z-index: 10;      
    border: 1px solid rgba(0,0,0,.4);  
}

.item:hover .item-title-container{
    display: block;
    visibility: visible;
}

.item-container{
    overflow: hidden;
    transition: .5s all ease-in-out;
    position: relative;
    background: #ccc;

    border: 1px solid rgba(0,0,0,0);  
}

.item-item-poster-container{
    position: absolute;
    top: 0;
    left: 0;
}

.card-body{
    display: none;
}

.item-title-container{
    display: none;
    visibility: hidden;
    padding: 1rem;
    background: #565353;
    color: #DDD;
}

.item-title-container h1{
    font-size: 1.2rem;
    margin: 0;
    padding: 0;
}

.item-title-container p{
    font-size: 0.75rem;
}

.item-poster{
    width: 100%;
    height: auto;
}

.card-text__filter{
    font-weight: bold;
    cursor: pointer;
    text-transform: capitalize;
}
.card-text__filter:hover{
    color: #FF0;
}
.card-text__filter:last-of-type{
    padding-right: .25rem;
}

.card-text--description{
    display: inline;
    padding-right: .25rem;
}

.card.film-card.watched::before{
    /* background: rgba(0,0,0,.5); */
    content:'';
    padding-right: 0.5rem;
    font-weight: bold;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    pointer-events: none;
}

.card.film-card.watched::after{
    content:'Watched';
    z-index: 3;
    font-weight: bold;
    display: block;
    position: absolute;
    color:#FFF; 
    pointer-events: none;
    text-align: center;
}

.icon-watched{
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    width: 6.25rem;
    height: 1.75rem;
    color: #000;
    background: top right / contain no-repeat url(/what-to-watch-react/static/media/watched-icon.6d099ba0.svg);
    cursor: pointer;
    outline: none;
    border: none;
}

.icon-watched:focus{ outline: none;border: none; }

.watched .icon-watched{
    top: 0;
    right: 0;   
    background: #363636;
    width: 100%;
}

body .card{
    border: none;
}

.card.film-card.watched:after, .card.film-card.watched:before {
    padding-right: .5rem;
    font-weight: 700;
    display: block;
    position: absolute;
    right: 0;
    pointer-events: none;
}

.card.film-card.watched:after {
    content: "Watched";
    font-size: 0.9rem;
    text-align: center;
    z-index: 3;
    /* top: 1.75rem; */
    color: #fff;
    width: 100%;
    padding: .25rem 0;
}

.btn-vod:hover{
    background: #FFF;
    color:#000;
}

.btn-trailer:hover{
    background: #FFF;
    color: #000;
}


@media (min-width: 30rem) and (max-width: 47.9375rem) {
    .item{
        flex-basis: calc(50% - .5rem);
        margin-left: .25rem;
        margin-right: .25rem;
        /* flex-basis: calc(50% - .5rem); */
    }

    .item-title-container h1{
        font-size: 1rem;
    }

    .item .item-container, .item:hover .item-container{
        position: relative;
        -webkit-transform: none;
                transform: none;
        overflow: visible;
        z-index: 10;        
    }
    
    .item .item-title-container, .item:hover .item-title-container{
        display: block;
        visibility: visible;
    }
}

@media (min-width: 48rem) {
    .item{
        flex-basis: calc(25% - .5rem);
        height: 9.625rem;
    }
}

@media (min-width: 64.5625rem) {
    .item{
        flex-basis: calc(20% - .5rem);
    }
}


.film-pagination{
    margin-top: 1rem;
    background: rgba(255,255,255,.3);
    background: linear-gradient(to right, 
        rgba(255,255,255,0) 0%,
        rgba(173,173,173,0.25) 25%,
        rgba(173,173,173,0.67) 50%,
        rgba(173,173,173,0.25) 75%,
        rgba(255,255,255,0) 100%);
}

.film-pagination .pagination{
    justify-content: center;
}

.film-pagination .page-item{
    padding: 0.5rem;
    background: #fff;
    border-right: 1px solid #CCC;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.film-pagination .page-item.page-active{
    background: #000;
    color: #FFF;
}

.film-pagination .page-item.disabled{
    cursor: none;
    pointer-events: none;
    background: rgba(0,0,0,0.1);
    color: #000;
    border:0;
}

.film-pagination .page-item:hover, .film-pagination .page-item:focus{
    background: #000;
    color: #FFF;
}

.film-pagination .page-item:last-child{
    border-right: 0;
}
footer{
    padding: 0.5rem 0;
    background: #FFF;
    text-align: center;
}

footer small{
    color: #000;
}
* { box-sizing: border-box; }
html body, body .container{
    background: #000;
}

body,html{
        margin: 0;
        padding: 0;
        font-family: Arial, Helvetica, sans-serif;
}

body.no-scroll{
    overflow: hidden;
}

body .container{
    padding: 1rem 0 0 0;
}

