.film-times{
    margin-bottom: 1rem;
    position: relative;
    border: 0;
    outline: none;
    cursor: pointer;
    padding-left: 0.5rem;
}

.film-times::before{
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #FFF;
    border: 1px solid #737373;
